.header-link{
  color: #fff;
  border: 2px solid #fff;
  display: inline-block;
  position: relative;
  padding: .8rem 1.6rem;
  font-weight: 700;
  line-height: 1;
  z-index: 1;
  text-decoration: none;
  transition: all .6s cubic-bezier(.19,1,.22,1);
}
.header-link:hover {
  color: lightseagreen;
  background-color: white;
}

#welcome-section {
  min-height: 100vh;
  height: 100vh;
  display: flex;
  align-items: center;
  border-bottom: 0;
  background: #fff;
  font-weight: 400;
  color: #272341;
  padding: 0 5.6rem;
  margin-bottom: 0;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: -1;
}

#opening-text {
  font-size: 40px;
  font-weight: 700;
}
#title {
position: absolute;
top: 50%;
left: 0;
right: 0;
color: #FFF;
text-align: center;
font-family: 'Raleway', sans-serif;
font-weight: 300;
font-size: 50px;
letter-spacing: 5px;
margin-top: -60px;
padding-left: 10px;
}

.header-text {
  width: 60%;
  margin: 0 auto;
}

@keyframes animStar {
from {
  transform: translateY(0px);
}
to {
  transform: translateY(-2000px);
}
}

.name-text-color{
  color: lightseagreen;
}

.header-section{
min-height: 100vh;
  height: 100vh;
  display: flex;
  align-items: center;
  border-bottom: 0;
  background: #fff;
  font-weight: 400;
  color: #272341;
  padding: 0 5.6rem;
  margin-bottom: 0;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: -1;
